<script setup>
import { ref, toRefs, onMounted } from 'vue'
import BaseCard from '@/src/components/BaseCard.vue'
import BaseSelect from '@/src/components/BaseSelect.vue'
import { SearchOutlined } from '@ant-design/icons-vue'

const searchInfo = ref({
  category: ''
})

const props = defineProps({
  libraries: {
    type: Array
  }
})

const { libraries } = toRefs(props)

const categoryList = [
  { value: 'bpmf1', label: '' },
  { value: 'bpmf2', label: '' },
  { value: 'bpmf3', label: '' },
  { value: 'bpmf4', label: '' },
  { value: 'bpmf5', label: '' },
  { value: 'bpmf6', label: '' },
  { value: 'bpmf7', label: '' },
  { value: 'bpmf8', label: '' },
  { value: 'bpmf9', label: '' },
  { value: 'bpmf10', label: '' },
  { value: 'bpmf11', label: '' },
  { value: 'bpmf12', label: '' },
  { value: 'bpmf13', label: '' },
  { value: 'bpmf14', label: '' },
  { value: 'bpmf15', label: '' },
  { value: 'bpmf16', label: '' },
  { value: 'bpmf17', label: '' },
  { value: 'bpmf18', label: '' },
  { value: 'bpmf19', label: '' },
  { value: 'bpmf20', label: '' },
  { value: 'bpmf21', label: '' },
  { value: 'bpmf22', label: '' },
  { value: 'bpmf23', label: '' },
  { value: 'bpmf24', label: '' },
  { value: 'bpmf25', label: '' },
  { value: 'bpmf26', label: '' },
  { value: 'bpmf27', label: '' },
  { value: 'bpmf28', label: '' },
  { value: 'bpmf29', label: '' },
  { value: 'bpmf30', label: '' },
  { value: 'bpmf31', label: '' },
  { value: 'bpmf32', label: '' },
  { value: 'bpmf33', label: '' },
  { value: 'bpmf34', label: '' },
  { value: 'bpmf35', label: '' },
  { value: 'bpmf36', label: '' },
  { value: 'bpmf37', label: '' }
]

onMounted(() => {
  const searchParams = new URLSearchParams(window.location.search)
  searchInfo.value.category = searchParams.get('category')

  if (!searchInfo.value.category) {
    searchInfo.value.category = 'bpmf1'
  }
})
</script>

<template>
  <div class="">
    <base-card>
      <div>
        <div class="mb-4 flex justify-between">
          <form
            action="/libraries"
            accept-charset="UTF-8"
            method="get"
            class="flex gap-2"
          >
            <BaseSelect
              :name="'category'"
              :optionList="categoryList"
              :optionsSelected="searchInfo.category"
              @select="searchInfo.category = $event"
            />
            <button
              type="submit"
              class="default-btn btn-gray"
            >
              <SearchOutlined />
              Search
            </button>
          </form>
        </div>
        <div class="grid grid-cols-4 gap-4 md:grid-cols-2">
          <div v-for="(word, index) in libraries" :key="index" class="rounded border border-gray-100 bg-gray-100">
            <div class="flex justify-center gap-1">
              <div class="py-2 text-lg text-green-500">{{ word.correct_word }}</div>
              <div class="py-2 text-lg text-red-500">{{ word.incorrect_word }}</div>
            </div>
          </div>
        </div>
      </div>
    </base-card>
  </div>
</template>
